.sidebar{
    flex: 3;
    height: calc(100vh - 50px);
    overflow: scroll;
}

.sidebarwrapper{
    padding: 20px;
   
}

.sidebarList{

    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebarListItem{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.sidebarIcon{

    margin-right: 15px;
}

.sidebarButton{
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 900;
}

.sidebarHr{

    margin: 20px 0;
}

.sidebarFriendList{

    padding: 0;
    margin: 0;
    list-style: none;
    

}

.sidebarListFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;

}

.sidebarFriendImg{

    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}